*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.navBar{
    width: 100vw;
    height: 150px;
    display: flex;
    flex-direction: row;
    align-items:center;
    border-bottom: 1px solid black;

}
.content-img{
    width: 20%;
    height: 100%;
}
.content-img img{
    width: 100%;
    height: 100%;
}
.list-menu{
    width: 70%;
    height: 30%;
    margin-right: 25px;
    display: flex;
    border-left: 1px solid black;

}
.list-menu ul{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    
}
.list-menu ul li{
    list-style: none;
    border-bottom: 1px solid black;
    cursor: pointer;
}
.cartWidget{
    border-left: 1px solid black;
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;

}